/**
* @description : This component is the main ui for @class Accounts
*
* @method render
* @method renderMainUI
* @method renderRows
* @method renderLoadingUI
* @method renderHelperComponents
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import { Card } from 'material-ui/Card';
import Avatar from 'material-ui/Avatar';
import TouchRipple from 'material-ui/internal/TouchRipple';
import Snackbar from 'material-ui/Snackbar';
import CircularProgress from 'material-ui/CircularProgress';

import { Accounts as AccountsString } from 'i18n/Persian';

export class AccountsUI extends React.Component {
  render() {
    return (
      <div style={{ marginTop: '10%', direction: 'rtl' }}>
        {this.renderMainUI()}
        {this.renderLoadingUI()}
        {this.renderHelperComponents()}
      </div>
    );
  }

  renderMainUI = () => {
    return (
      <Row center="xs" className={this.props.states.rootClassName}>
        <Col lg={4} md={4} sm={6} xs={11}>
          <Card>
            <h1 style={{ fontSize:'1.1rem', textAlign: 'right', padding: 16 }}>
              {AccountsString.title}
            </h1>

            {this.renderRows()}
          </Card>
        </Col>
      </Row>
    );
  };

  /**
  * @description : Renders card rows with university and user data in each of them
  *
  * @author Armin Eslami
  *
  * @return jsx element
  */
  renderRows = () => {
    if (!this.props.data || !this.props.data.universities) {
      return null;
    }

    var rows = this.props.data.universities.map(uni => {
      return (
        <Row center="xs" key={uni.uniCode}>
          <Col xs={11}>
            <div
              style={{
                position: 'relative',
                textAlign: 'right',
                background: '#eeeeee',
                marginBottom: 16,
                borderRight: '4px solid rgb(255, 64, 129)',
                cursor: 'pointer'
              }}
              onTouchTap={() => this.props.handlers.onRowTouchTap(uni)}
            >
              <TouchRipple>
                <Row>
                  <Col xs={3} style={{ position: 'relative' }}>
                    {this.props.data.avatar
                      ? <Avatar
                          size={64}
                          style={{
                            position: 'absolute',
                            top: 8,
                            left: 8,
                            right: 0,
                            margin: 'auto'
                          }}
                          src={
                            process.env.REACT_APP_DOMAIN +
                            process.env.REACT_APP_IMAGES_DIRECTORY +
                            this.props.data.avatar
                          }
                        />
                      : <Avatar
                          size={64}
                          style={{
                            position: 'absolute',
                            top: 8,
                            left: 8,
                            right: 0,
                            margin: 'auto'
                          }}
                        >
                          {this.props.data.firstName.substring(0, 1)}
                        </Avatar>}
                  </Col>
                  <Col xs={9}>
                    <p>
                      {this.props.data.firstName +
                        ' ' +
                        this.props.data.lastName}
                    </p>
                    <p>
                      {uni.uniName}
                    </p>
                  </Col>
                </Row>
              </TouchRipple>
            </div>
          </Col>
        </Row>
      );
    });

    return rows;
  };

  /**
  * @description : Renderes a overlay loading ui
  *
  * @author Armin Eslami
  *
  * @return jsx element
  */
  renderLoadingUI = () => {
    return this.props.states.isSwitchingAccount
      ? <div
          style={{
            width: '100%',
            heigth: '100%',
            backgroundColor: 'rgba(0,0,0,0.5)',
            position: 'fixed',
            top: 0,
            right: 0,
            left: 0,
            bottom: 0,
            zIndex: 999
          }}
        >
          <CircularProgress
            style={{
              position: 'absolute',
              left: 0,
              right: 0,
              top: 0,
              bottom: 0,
              margin: 'auto'
            }}
            size={60}
            thickness={5}
          />
        </div>
      : void 0;
  };

  renderHelperComponents = () => {
    return (
      <div>
        <Snackbar
          open={this.props.states.isSnackOpen}
          message={this.props.states.snackMessage}
          autoHideDuration={3000}
          onRequestClose={this.props.handlers.onSnackClose}
          contentStyle={{ textAlign: 'center' }}
        />
      </div>
    );
  };
}

export default AccountsUI;



