/**
* @description : This component is responsible for choosing/switching accounts
*
* @method constructor
* @method render
* @method handleRowTouchTap
* @method handleSnackClose
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { connect } from 'react-redux';
import { hashHistory } from 'react-router';
import cookie from 'react-cookie';

import BaseComponent from 'components/base/BaseComponent';
import AccountsUI from './AccountsUI';
import { updateUser } from 'redux/actions/UserActions';
import { selectAccount } from 'api/Board';
import { Global as GlobalStrings } from 'i18n/Persian';

export class Accounts extends BaseComponent {
  constructor(props) {
    super(props);

    // Css transition delay
    this.TRANSITION_DELAY = 1000;
    // Days to save cookie
    this.COOKIE_EXPIRE_DAYS = 30; // REMOVE

    // Component default states
    this.DEFAULT_STATE = {
      rootClassName: 'right-to-left-move-in', // To hold main root element class name
      isSwitchingAccount: false, // To track whether user has picked his account or not
      isSnackOpen: false, // To track whether snack is opened or not
      snackMessage: '' // To hold snack message
    };

    // Changing these data wont re-render the components
    this.data = { ...this.props.user };

    // Handlers for interacting with ui
    this.handlers = {
      onRowTouchTap: this.handleRowTouchTap,
      onSnackClose: this.handleSnackClose
    };

    this.state = this.DEFAULT_STATE;
  }

  render() {
    return (
      <div>
        <AccountsUI
          states={this.state}
          data={this.data}
          handlers={this.handlers}
        />
      </div>
    );
  }

  /**
  * @description : Handles universities card click when user is registered on more than one university
  *
  * @author Armin Eslami
  *
  * @param uniData (object) : An object containing user's picked university e.g : unCode, uniName, ...
  */
  handleRowTouchTap = uniData => {
    this.setState({ isSwitchingAccount: true });

    selectAccount(
      this.props.user.token,
      uniData.uniCode,
      uniData.type,
      this.props.user.rememberMe
    ).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('selectAccount() ended with result', res)
          : void 0;

        if (res.code === 200 && res.data) {
          // REMOVE: Remove this ! because server should set the cookie if the remember me is checked
          // Or server should return the cookie token with the other data too

          if (this.props.user.rememberMe) {
            var date = new Date();
            date.setTime(
              date.getTime() + this.COOKIE_EXPIRE_DAYS * 24 * 60 * 60 * 1000
            );

            cookie.save('_wr', 'COOKIE_TOKEN', {
              path: '/',
              expires: date
            });
          }

          this.props.dispatch(updateUser({ isLoggedIn: true, type:uniData.type || undefined }));

          this.setState({
            isSwitchingAccount: false,
            rootClassName: 'top-to-bottom-move-out'
          });

          setTimeout(function() {
            hashHistory.replace('/');
          }, this.TRANSITION_DELAY);
        } else if (res.code === 400 && res.errors && res.errors[0].message) {
          this.setState({
            isSwitchingAccount: false,
            snackMessage: res.errors[0].message,
            isSnackOpen: true
          });
        } else {
          this.setState({
            snackbarMessage: GlobalStrings.fatalError,
            isSnackOpen: true,
            isSwitchingAccount: false
          });
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('selectAccount() ended with error', err)
          : void 0;
      }
    );
  };

  /**
  * @description : Handler for closing snack
  *
  * @author Armin Eslami
  */
  handleSnackClose = () => {
    this.setState({ isSnackOpen: false });
  };
}

export default connect(state => {
  return {
    user: state.user
  };
})(Accounts);



